import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import xueshengList from '@/views/pages/xuesheng/list'
import xueshengDetail from '@/views/pages/xuesheng/formModel'
import xueshengAdd from '@/views/pages/xuesheng/formAdd'
import xueshengRegister from '@/views/pages/xuesheng/register'
import xueshengCenter from '@/views/pages/xuesheng/center'
import qiyeList from '@/views/pages/qiye/list'
import qiyeDetail from '@/views/pages/qiye/formModel'
import qiyeAdd from '@/views/pages/qiye/formAdd'
import jianliList from '@/views/pages/jianli/list'
import jianliDetail from '@/views/pages/jianli/formModel'
import jianliAdd from '@/views/pages/jianli/formAdd'
import gangweileixingList from '@/views/pages/gangweileixing/list'
import gangweileixingDetail from '@/views/pages/gangweileixing/formModel'
import gangweileixingAdd from '@/views/pages/gangweileixing/formAdd'
import zhaopinxinxiList from '@/views/pages/zhaopinxinxi/list'
import zhaopinxinxiDetail from '@/views/pages/zhaopinxinxi/formModel'
import zhaopinxinxiAdd from '@/views/pages/zhaopinxinxi/formAdd'
import wodeshenqingList from '@/views/pages/wodeshenqing/list'
import wodeshenqingDetail from '@/views/pages/wodeshenqing/formModel'
import wodeshenqingAdd from '@/views/pages/wodeshenqing/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'xueshengList',
			component: xueshengList
		}, {
			path: 'xueshengDetail',
			component: xueshengDetail
		}, {
			path: 'xueshengAdd',
			component: xueshengAdd
		}
		, {
			path: 'xueshengCenter',
			component: xueshengCenter
		}
		, {
			path: 'qiyeList',
			component: qiyeList
		}, {
			path: 'qiyeDetail',
			component: qiyeDetail
		}, {
			path: 'qiyeAdd',
			component: qiyeAdd
		}
		, {
			path: 'jianliList',
			component: jianliList
		}, {
			path: 'jianliDetail',
			component: jianliDetail
		}, {
			path: 'jianliAdd',
			component: jianliAdd
		}
		, {
			path: 'gangweileixingList',
			component: gangweileixingList
		}, {
			path: 'gangweileixingDetail',
			component: gangweileixingDetail
		}, {
			path: 'gangweileixingAdd',
			component: gangweileixingAdd
		}
		, {
			path: 'zhaopinxinxiList',
			component: zhaopinxinxiList
		}, {
			path: 'zhaopinxinxiDetail',
			component: zhaopinxinxiDetail
		}, {
			path: 'zhaopinxinxiAdd',
			component: zhaopinxinxiAdd
		}
		, {
			path: 'wodeshenqingList',
			component: wodeshenqingList
		}, {
			path: 'wodeshenqingDetail',
			component: wodeshenqingDetail
		}, {
			path: 'wodeshenqingAdd',
			component: wodeshenqingAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/xueshengRegister',
		component: xueshengRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
